@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap');

/* subscribe section start here ========================= */


.subscribe{
    width: 90%;
    margin:80px auto;
    max-width: 1266px;
    background-color: #EEFBF3;
    padding: 70px;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.subscribe .left,
.subscribe .right{
    width: 50%;
}
.subscribe .left h1{
    font-size: 50px;
    font-weight: bold;
    font-family: 'Open Sans', sans-serif;
}

.subscribe .right {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.subscribe .right .rightInner{
    text-align: center;
}
.subscribe .right .rightInner input{  
    padding: 10px 15px;
    margin: 15px 0px;
    border: 1px solid #309255;
    box-shadow: none;
    width: 300px;
    outline: none;
    font-family: 'Open Sans', sans-serif;
}

.subscribe .right .rightInner button{
    padding: 10px 25px;
    background-color: #309255;
    color: #fff;
    border: 1px solid #309255;
    cursor: pointer;
    border-radius: 5px;
    font-family: 'Open Sans', sans-serif;
}


/* footer section start here =================== */

.footerTop{
    width: 100%;
    background-color: #EEFBF3;
    padding: 25px 0px;
}
.footerTop .footerInner{
  width: 90%;
  margin: auto;
  max-width: 1266px;
  display: flex;
  justify-content: space-between;
}

.footerTop .footerInner .logo,
.footerTop .footerInner .subscribeF,
.footerTop .footerInner .quickLinks,
.footerTop .footerInner .category{
    width: 25%;
}

